<template>
    <div>

         <!--  -->
        <el-breadcrumb separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>

        <el-card class="box-card">
            <!--  -->
            <el-row :gutter="20">
                <el-col :span="8">
                    <!--  -->
                    <el-input
                     placeholder=""
                     v-model="queryInfo.query"
                     clearable
                     @clear="getOrderslist">
                        <el-button slot="append" icon="el-icon-search" circle @click="getOrderslist"></el-button>
                    </el-input>

                </el-col>
            </el-row>

            <!--  -->
            <el-table :data="orderslist"  border stripe>
                <el-table-column type="index" width="50" label="#"></el-table-column>
                <el-table-column prop="order_number" width="450px" label=""></el-table-column>
                <el-table-column prop="order_price" label=""></el-table-column>
                <el-table-column label="">
                    <template v-slot="scope">
                        <div>
                            <el-tag type="danger" v-if="scope.row.order_pay === '0'"></el-tag>
                            <el-tag type="success" v-else></el-tag>
                        </div>
                    </template>
                </el-table-column>
                <el-table-column prop="is_send" label=""></el-table-column>
                <el-table-column label="" width="180px">
                    <template v-slot="scope">
                            {{scope.row.create_time | dateFormat}}
                    </template>
                </el-table-column>
                <el-table-column label="">
                   <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"></el-button>
                   <el-button type="success" size="mini" icon="el-icon-map-location" @click="showProgressBox"></el-button>
                </el-table-column>
            </el-table>

            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="queryInfo.pagenum"
                :page-sizes="[5,  10, 15]"
                :page-size="queryInfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">

            </el-pagination>

        </el-card>

        <!--  -->
        <el-dialog
            title=""
            :visible.sync="addressVisible"
            @close="addressDialogClosed">
           <el-form :model="addressForm" :rules="addressFormRules" ref="addressFormRef" label-width="100px">
                <el-form-item label="" prop="address1">
                    <el-cascader :options="cityData" v-model="addressForm.address1"></el-cascader>
                </el-form-item>
                <el-form-item label="" prop="address2">
                    <el-input v-model="addressForm.address2"></el-input>
                </el-form-item>
           </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addressVisible = false"> </el-button>
                <el-button type="primary" @click="addressVisible = false"> </el-button>
            </span>
        </el-dialog>

        <!--  -->
        <el-dialog
            title=""
            :visible.sync="progressVisible"
            width="30%">
            <el-timeline>
                <el-timeline-item
                v-for="(activity, index) in progressInfo"
                :key="index"
                :timestamp="activity.time">
                {{activity.context}}
                </el-timeline-item>
            </el-timeline>
            <span slot="footer" class="dialog-footer">
                <el-button @click="progressVisible = false"> </el-button>
                <el-button type="primary" @click="progressVisible = false"> </el-button>
            </span>
        </el-dialog>

    </div>
</template>

<script>
import cityData from './citydata'
export default {
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      orderslist: [],
      total: 0,
      addressVisible: false,

      addressForm: {
        address1: [],
        address2: ''
      },

      //  
      addressFormRules: {
        address1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cityData,

      progressVisible: false,

      progressInfo: []
    }
  },
  created () {
    this.getOrderslist()
  },
  methods: {
    //  
    async getOrderslist () {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // this.$message.success('')
      this.orderslist = res.data.goods
      this.total = res.data.total
    //   console.log(this.orderslist)
    },

    // 
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderslist()
    },

    // 
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
      this.getOrderslist()
    },

    // 
    showBox () {
      this.addressVisible = true
    },

    addressDialogClosed () {
      this.$refs.addressFormRef.resetFields()
    },

    async showProgressBox () {
      const ips = '804909574412544580'
      const { data: res } = await this.$http.get('/kuaidi/' + ips)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.progressInfo = res.data
      this.progressVisible = true
    //   console.log(this.progressInfo)
    }
  }
}
</script>

<style lang="less" scoped>
.el-cascader{
    width: 60%;
}
</style>
